.login {
  height: calc(100vh - 125px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)),
    url("https://images.pexels.com/photos/3784221/pexels-photo-3784221.jpeg?auto=compress&cs=tinysrgb&h=650&w=940");
  background-size: cover;
}

@media screen and (max-width: 400px) {
  .login {
    height: calc(100vh - 125px - 70px);
  }
}

.loginTitle {
  font-size: 50px;
}

.loginForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.loginForm > label {
  margin: 10px 0;
}

.loginInput {
  padding: 10px;
  background-color: white;
  border: none;
}

.loginButton {
  margin-top: 20px;
  cursor: pointer;
  background-color: lightcoral;
  border: none;
  color: white;
  border-radius: 4px;
  padding: 10px;
}

.loginButton:disabled {
  cursor: not-allowed;
  background-color: rgb(255, 142, 142);
}

.loginRegisterButton {
  position: absolute;
  top: 60px;
  right: 20px;
  border: none;
  padding: 10px;
  border-radius: 4px;
  background-color: teal;
  color: white;
}
