.singlePost {
  flex: 9;
}

.singlePostWrapper {
  margin: 20px;
  padding-right: 0;
  display: flex;
  flex-direction: column;
}

.singlePostImg {
  border-radius: 10px;
  width: 100%;
  height: 300px;
  /* background-repeat: 5px; */
  object-fit: cover;
}

.singlePostTitle {
  text-align: center;
  margin: 10px;
  /* font-family: 'Lora', serif; */
  font-size: 28px;
}

.singlePostTitleInput {
  margin: 10px;
  /* font-family: 'Lora', serif; */
  font-size: 28px;
  border: none;
  border-bottom: 1px solid #222;
  color: #222;
}

.singlePostTitleInput:focus {
  outline: none;
}

.singlePostEdit {
  float: right;
  font-size: 16px;
}

.singlePostIcon {
  margin-left: 10px;
  cursor: pointer;
}

.singlePostIcon:first-child {
  color: rgb(0, 105, 105);
}

.singlePostIcon:last-child {
  color: rgb(167, 25, 0);
}

.singlePostInfo {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  /* font-family: 'Varela Round', sans-serif; */
  color: #e3c586;
}

.singlePostDesc {
  color: #222;
  font-size: 18px;
  line-height: 25px;
}

.singlePostDescInput {
  border: none;
  color: #222;
  font-size: 18px;
  line-height: 25px;
}

.singlePostDescInput:focus {
  outline: none;
}

.singlePostDesc::first-letter {
  margin-left: 20px;
  font-size: 30px;
  font-weight: 600;
}

.singlePostButton {
  border: none;
  background-color: teal;
  padding: 5px;
  width: 100px;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  align-self: flex-end;
  margin-top: 15px;
}
